
/*
  Global variables
  Every time getString is called
  it can keep a state (index)
*/

let theString = 'Welcome to my app, hope you enjoy the visit      '; //A default value
let length = theString.length;
let index = 0;
const maxChar = 20;     //20 Chars on screen

/*
  Setter method, to correctly place a new string
*/
export const setNewString = (string)=>{

  theString = string + '      ';
  length = theString.length;
  index = 0; //Reset the index, to start a new sentence

}

/*
  This function will return a 20 char long string, use the value
  returned, to update the state of a Component
*/
export const getString = () =>{
  let currentString = '';
  if((index + maxChar) < length){
    currentString = theString.slice(index, index+maxChar);
    index ++;
  }else{
    currentString = theString.slice(index, length);
    let indexLeft = maxChar - currentString.length;
    //Chars left to fill the maxChar requeriments
    currentString += theString.slice(0, indexLeft);
    
    if(index === length){
      index = 0; //If max index reached, means that it can reset
    }else{  
      index ++;
    }
  } 
  return currentString;
}

